@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(//db.onlinewebfonts.com/c/23c0fcab84d99da0de762de7e220a6e1?family=Europa);
@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Styles for the Home React component */
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bg-image {
  background-size: cover;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), url(/radhikalakhtakia/static/media/rads1.1d6aae2c.jpg);
  background-position: center;
}

.middle {
  width: 75%;
  height: 100%;
  margin-top: 6%;
  background-color: #c9c9c9;
}

.title {
  font-size: 58px;
  font-weight: 900;
  color: #111a29;
}

.subtitle {
  margin-top: -40px;
  font-size: 28px;
  font-weight: 700;
  color: #111a29;
}

.hero-text {
  display: table;
  margin: 0 auto;
  margin-top: 15px;
}

.home__button-link {
  text-decoration: none;
}

.MuiButton-root.__button {
  width: 200px;
  height: 40px;
  font-size: 18px;
  font-weight: 700;
  color: #111a29;
}

/* Increase specificity to override component styles */
.MuiButton-root.__button:hover {
  background-color: transparent;
  border-bottom: 4px #111a29 solid;
  border-radius: 0;
}

/* Styles for the Header React component */
.header {
  margin-top: 2%;
  margin-bottom: 2%;
  height: 43.5px;
  text-align: center;
  background: #E8DBD8;
  vertical-align: middle;
}

.button {
	text-decoration: none;
	padding-top: 11px;
	padding-bottom: 11px; 
	font-size: 18px;
	font-weight: 500;
	color: #111a29;
  text-align: center;
  border-radius: 20px;
  width: 200px;
  display: inline-block;
    
}

.button.active, .button:hover {
	background-color: #111A29;
	color: #E8DBD8;
	border-radius: 0;
}

/* Styles for the Home React component */
body {
