}

.btn {
  padding: 10px;
  background-color: #111a29;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 14px;
  text-align: center;
  margin-left: 5%;
  padding-right: 30px;
  text-decoration: none;
  font-weight: 400;
}

/* Darker background on mouse-over */
.btn:hover {
  background-color: #202836;
}
input[type=text], input[type=email], textarea, select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: #E8DBD8;
  font-size: 14px;
  font-weight: 400;
  font-family: sans-serif;
}

input[type=submit] {
  width: 13%;
  background-color: #E8DBD8;
  border: 2px #111a29 solid;
  color: #111a29;
  padding: 14px 20px;
  margin: 8px 0;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
}

input[type=submit]:hover {
  background-color: #111a29;
  color: #E8DBD8;
}

